<template>
    <div class="Table">
        <el-container>
            <el-header>
                <el-row>
                    <el-button type="danger" width="30%"
                               position="relative" @click="toMain"></el-button>


                    <el-button type="danger" width="30%"
                               position="relative" @click="toTable"></el-button>

                </el-row>

            </el-header>
            <el-main>
                <el-input v-model="search" style="width: 30%"
                          placeholder="" prefix-icon="el-icon-search"/>

                <el-table :data="userData.filter(data=>!search || data.name.includes(search))"
                          stripe
                          style="width: 100%">
                    <el-table-column>
                        <!--<template slot="header"  scope="scope" >-->
                        <!--<el-input v-model="search" style="width: 300px"-->
                        <!--placeholder="" prefix-icon="el-icon-search"/>-->
                        <!--</template>-->
                        <el-table-column
                                prop="user_id"
                                label=""
                                sortable
                        >
                        </el-table-column>
                        <el-table-column
                                prop="name"
                                label=""
                                sortable
                        ></el-table-column>

                        <el-table-column
                                prop="status"
                                label=""
                                sortable
                        ></el-table-column>
                        <el-table-column
                                prop="usable"
                                label=""
                                sortable
                        ></el-table-column>
                        <el-table-column
                                align="right">
                            <template slot-scope="scope">
                                <el-button
                                        size="mini"
                                        @click="unabled(scope.row)"></el-button>

                                <el-button
                                        size="mini"
                                        type="danger"
                                        @click="abled(scope.row)"></el-button>
                            </template>
                        </el-table-column>
                    </el-table-column>
                </el-table>
            </el-main>
            <el-footer></el-footer>
        </el-container>
    </div>
</template>

<script>
    export default {
        name: "userManager",
        data () {
            return {
                search:null,

                userData:[],

            }
        },
        mounted(){
            this.loadUser();
        },
        methods: {

            toMain(){
                this.$router.push({ path: `/` });
            },
            toTable(){
                this.$router.push({ path: `/Table` });
            },
            unabled(row){
                var that = this;
                this.$myAxios.get('/ebook/unableUser',{
                    params:{
                        id:row.user_id,

                    }
                })
                    .then(function (response) {
                        console.log(response);
                    })
                    .catch(function (error) {
                        console.log(error);
                    })
                    .then(function () {
                        //always executed
                        that.userData=[];
                        that.$options.methods.loadUser.bind(that)();
                    })
            },
            abled(row){
                var that = this;
                this.$myAxios.get('/ebook/ableUser',{
                    params:{
                        id:row.user_id,

                    }
                })
                    .then(function (response) {
                        console.log(response);
                    })
                    .catch(function (error) {
                        console.log(error);
                    })
                    .then(function () {
                        //always executed
                        that.userData=[];
                        that.$options.methods.loadUser.bind(that)();
                    })
            },
            loadUser:function () {
                var that = this;
                this.$myAxios.get('/ebook/allUsers')
                    .then((response)=> {
                        for(var i=0;i<response.data.length;i++){
                            var objproject = {
                                "user_id" : response.data[i].id,//
                                "name" : response.data[i].name,
                                "status" :response.data[i].status,
                                "usable" :response.data[i].usable
                            }
                            that.userData.push(objproject);
                            //console.log(that.bookData[i]);

                            //console.log(that.bookData[i].title);

                        }
                        this.$forceUpdate();//

                    })
                    .catch(function (error) {
                        console.log(error);  // 
                    });
            },
        }
    }
</script>

